import {
  createRouter,
  RouteLocationNormalized,
  RouteLocationNormalizedLoaded,
  createWebHashHistory,
} from 'vue-router'

declare type Position = { x: number; y: number }
declare type PositionResult =
  | Position
  | { selector: string; offset?: Position }
  | void
declare type Awaitable<T> = T | Promise<T>

const scrollBehavior = (
  to: RouteLocationNormalized,
  from: RouteLocationNormalizedLoaded,
  savedPosition: Required<Position> | null
): Awaitable<PositionResult | false | void> => {
  return savedPosition && to.meta['keepAlive'] ? savedPosition : { x: 0, y: 0 }
}

const router = createRouter({
  history: createWebHashHistory(),
  routes: [
    {
      path: '/',
      component: () => import('./components/pages/Page.vue'),
    },
    {
      path: '/:year/:month/:day/:post',
      component: () => import('./components/pages/PostDetail.vue'),
      props: (route: RouteLocationNormalized) => ({
        title: router.currentRoute.value.params['post']
          .toString()
          .replace(' ', ''),
      }),
    },
    {
      path: '/page/:page',
      component: () => import('./components/pages/Page.vue'),
      props: (route: RouteLocationNormalized) => ({
        title: `第${router.currentRoute.value.params['page']}页`
      }),
    },
    {
      path: '/about',
      component: () => import('./components/pages/About.vue'),
    },
    { path: '/*', redirect: '/' },
  ],
  scrollBehavior: scrollBehavior,
})

router.beforeEach((to, from, next) => {
  document.title = String(to.params['post']).replace(' ', '');
  next();
})

export default router
