<template>
  <div class="wrapper">
    <div class="page">
      <post-block class="post" v-for="post in postList" v-bind:key="post.id" :post="post" />
      <div class="pageOrder">
        <v-button
          class="prepage button"
          v-bind:link="prepage"
          v-bind:text="prePageText"
        />
        <v-button
          class="nextpage button"
          v-bind:link="nextpage"
          v-bind:text="nextPageText"
        />
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import axios, { AxiosResponse } from 'axios'
import { reactive, ref, watch, onActivated, SetupContext, computed } from 'vue'
import router from '../../router'
import { Page, Post } from '../../store/state'
import PostBlock from '../widgets/PostBlock.vue'
import Button from '../widgets/Button.vue'

export default {
  components: {
    'post-block': PostBlock,
    'v-button': Button,
  },
  setup() {
    const prepage = ref<String>('')
    const nextpage = ref<String>('')
    const postList = ref('')
    const prePageText = ref('上一页')
    const nextPageText = ref('下一页')

    function refreshPage() {
      const pageIndex = router.currentRoute.value.params['page'] ?? 0
      axios.get(`/page-${pageIndex}.json`).then((res: AxiosResponse) => {
        prepage.value = String(res.data.prepage)
        nextpage.value = String(res.data.nextpage)
        postList.value = res.data.postList
      })
    }

    watch(router.currentRoute, refreshPage);
    refreshPage();

    return { prepage, nextpage, postList, prePageText, nextPageText }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scope>
.wrapper {
    min-width: 23.4375rem;

    .page {
        width: 100%;
        .post {
            margin-bottom: .625rem;
        }

        .pageOrder {
            width: 80%;
            overflow: hidden;
            margin: 0 auto;

            .button {
                padding: 0px auto;
            }

            .prepage {
                float: left;
            }

            .nextpage {
                float: right;
            }
        }

        @media only screen and (min-width: 1366px) {
            .pageOrder {
                width: 50%;
            }
        }
    }
}
</style>
