
import { ref, Ref, reactive } from 'vue'
import PostBlock from '../widgets/PostBlock.vue'
import { Post, PostMd5Argument } from '../../store/state'
import { PageParams } from '../utils/routerparams'
import axios, { AxiosResponse } from 'axios';
import router from '../../router'
import { RouteLocationNormalizedLoaded } from 'vue-router';

export default {
  components: {
    'post-block': PostBlock
  },
  setup() {
    const year = router.currentRoute.value.params["year"];
    const month = router.currentRoute.value.params["month"];
    const day = router.currentRoute.value.params["day"];
    const title = router.currentRoute.value.params["post"].toString().replace(" ", "");

    let props = reactive({
      post: {
        id: String,
        body: String,
        attributes: {
          title: String,
          date: Date,
          author: String,
        }
      }
    });

    axios.get(`${year}/${month}/${day}/${title}.json`).then((res: AxiosResponse) => {
        props.post = res.data;

    });

    return { props };
  },
}
